.modal {
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(102, 102, 102, 0.5);
    z-index: 100;
}
.modalInner {
    background-color: white;
    display: flex;
    flex-direction: column;
}
.modalHeader {
    width: 100%;
    height: 49px;
    flex-shrink: 0;
    padding: 0 20px;
    box-sizing: border-box;
    border-bottom: 1px solid #d9d9d9;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 18px;
    font-weight: bold;
}

.modalFooter {
    width: 100%;
    height: 50px;
    position: relative;
    flex-shrink: 0;
    border-top: 1px solid #d9d9d9;
    display: flex;
    justify-content: center;
    align-items: center;
}
.modalBody {
    width: 100%;
    height: 100%;
    display: flex;
    overflow: hidden;
    flex-direction: column;
}
.modalClose {
    cursor: pointer;
}
.modalCancel {
    width: 70px;
    height: 26px;
    border-radius: 2px;
    color: #333333;
    border: 1px solid #d9d9d9;
    background-color: #ffffff;
    margin-right: 12px;
    cursor: pointer;
}
.modalOk {
    width: 70px;
    height: 26px;
    border-radius: 2px;
    color: #ffffff;
    border: 1px solid #5582f3;
    background-color: #5582f3;
    margin-right: 12px;
    cursor: pointer;
}

@ant-prefix: _ant;