.displayArea {
    width: 100%;
    height: 100%;
    display: flex;
    overflow: hidden;
    flex-direction: column;
    .displayNav {
        height: 50px;
        width: 100%;
        flex-shrink: 0;
        background: #ffffff;
        border-bottom: 1px solid #e5e5e5;
        .backHomePage {
            font-size: 25px;
            margin-left: 20px;
            cursor: pointer;
            font-size: 18px;
        }
        .zoomArea {
            width: 150px;
            height: 100%;
        }
        .operateRightNav {
            width: auto;
            height: 100%;
            display: flex;
        }
        .startPaint {
            width: 100px;
            font-size: 12px;
            height: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            border-left: 1px solid #e5e5e5;
        }
        .showListBox {
            width: 50px;
            height: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            border-left: 1px solid #e5e5e5;
        }
    }
    .displayBody {
        height: 100%;
        width: 100%;
        display: flex;
        overflow: hidden;
        flex-direction: column;
        .header {
            height: 50px;
            width: 100%;
            flex-shrink: 0;
            display: flex;
            font-size: 14px;
            > span {
                margin-left: 20px;
            }
        }
        .body {
            height: 100%;
            width: 100%;
            display: flex;
            flex-direction: column;
            overflow: scroll;
            position: relative;
        }
    }
}
.upDownBtn {
    width: 17px;
    height: 17px;
    cursor: pointer;
    margin: 10px;
}
.pre {
    width: 50px;
    height: 25px;
    font-size: 17px;
    text-align: center;
    color: #212121;
    line-height: 25px;
    display: flex;
}

@ant-prefix: _ant;