@import url("../color.less");

/* * {
    // transition: all linear 0.1s;
} */
.tie {
    width: 100%;
    height: 100%;
    // overflow: hidden;
    position: absolute;
    box-sizing: border-box;
    background-color: @backgroundColor_gray;
}

::-webkit-scrollbar-thumb {
    min-height: 28px;
    height: 5px;
    min-width: 2px;
    width: 5px;
    border-radius: 4px;
    border: dashed transparent;
    padding: 100px 0 0;
    border-width: 1px;
    background-color: @backgroundColor_scroll;
    background-clip: padding-box;
}

::-webkit-scrollbar {
    width: 8px !important;
    height: 8px !important;
    overflow: visible;
}

:global {
    .ant-message {
        z-index: 99999;
    }
}
@ant-prefix: _ant;