.tableWrapper {
  width: 100%;
  height: 100%;
  display: flex;
  overflow: hidden;
  background: #ffffff;
  flex-direction: column;
}
.tableNav {
  width: 100%;
  flex-shrink: 0;
  height: 30px;
  display: flex;
  padding: 0 20px;
  align-items: center;
  justify-content: space-between;
  font-size: 12px;
  box-sizing: border-box;
}
.tableBody {
  width: 100%;
  height: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: row;
}
.tableScrollAreaHiddenY {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.tableScrollArea {
  width: 100%;
  height: 100%;
  padding-right: 15px; /* Maximum width of scrollbar */
  overflow: scroll;
  display: flex;
  flex-direction: column;
}
.tableItemBasic {
  color: #333333;
}
.radioCol {
  min-width: 30px;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  .selectRadioOuterRing {
    width: 12px;
    height: 12px;
    border: 1px solid #d9d9d9;
    background: #ffffff;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .selectRadioOuterRing:hover {
    border: 1px solid #5582f3;
    cursor: pointer;
  }

  .selectRadioInnerRing {
    background: #ffffff;
    width: 0;
    height: 0;
    -webkit-transition: all calc(0.3s - 0.1s) cubic-bezier(0.4, 0, 0.6, 1);
    transition: all calc(0.3s - 0.1s) cubic-bezier(0.4, 0, 0.6, 1);
  }
}
.columns {
  width: auto;
  background: #f2f2f2;
  height: 34px;
  display: flex;
  flex-shrink: 0;
  padding: 0 8px;
  // box-sizing: border-box;
}
.tableItemTabs {
  font-size: 12px;
  padding: 0 12px;
  box-sizing: border-box;
  display: flex;
  position: relative;
  justify-content: flex-start;
  background: #f2f2f2;
  border-bottom: 1px solid #d9d9d9;
  align-items: center;
}
.tableItemBasic {
  font-size: 12px;
  padding: 0 12px;
  box-sizing: border-box;
  display: flex;
  position: relative;
  justify-content: flex-start;
  align-items: center;
}
.tableItemBasicContent {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.tableItemBasic:after {
  content: "";
  position: absolute;
  right: 0;
  /* left: 0; */
  /* top: -2px; */
  width: 1px;
  height: 16px;
  background-color: #d9d9d9;
}
.tableDataArea {
  width: 100%;
  height: 100%;
  // overflow-y: scroll;
  // overflow-x: scroll;
  display: flex;
  flex-direction: column;
}
.dataSourceItemWrapper {
  width: 100%;
  height: 41px;
  // flex-grow: 1;
  display: flex;
  flex-shrink: 0;
  padding: 0 8px;
  box-sizing: border-box;
  background: #ffffff;
  border-bottom: 1px solid #d9d9d9;
}
.dataSourceItemWrapper:hover {
  background-color: #f2f4ff;
}
.PageTurningTool {
  width: 100px;
  height: 20px;
  background-color: #ffffff;
  border: 1px solid #d9d9d9;
  display: flex;
  justify-content: space-between;
  align-items: center;
  user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
}
.PageBtn {
  height: 100%;
  width: 15px;
  display: flex;
  cursor: pointer;
  justify-content: center;
  align-items: center;
  > img {
    width: 10px;
  }
}
.PageTurning {
  height: 100%;
  width: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.frozeTableCol {
  width: auto;
  height: 100%;
  display: flex;
  flex-direction: column;
}

@ant-prefix: _ant;