@import url("../color.less");
.operatingInterface {
    width: 100%;
    height: 100%;
    display: flex;
}
.operatingDisplay {
    width: 100%;
    height: 100%;
}
.operatingConsole {
    width: 250px;
    height: 100%;
    flex-shrink: 0;
    display: flex;
    position: relative;
    background: @backgroundColor_white;
    flex-direction: column;
}
.navigationBar {
    width: 100%;
    height: 100%;
    flex-direction: column;
    .navigationBarConsole {
        width: 100%;
        height: auto;
        display: flex;
        flex-direction: column;
        .navItemWrapper {
            width: 100%;
            height: 50px;
            display: flex;
            box-sizing: border-box;
            padding: 0 20px;
            color: #212121;
            justify-content: flex-start;
            align-items: center;
            cursor: pointer;
            user-select: none;
            -webkit-user-select: none;
            -ms-user-select: none;
            -moz-user-select: none;
            -webkit-user-drag: none;
            > img {
                width: 28px;
            }
            > span {
                font-size: 14px;
                margin-left: 10px;
            }
            .iconSize {
                font-size: 20px;
            }
        }
        .activeNav {
            color: #5582f3;
            background-color: #f2f6ff;
            border-right: 3px solid #5582f3;
        }
    }
    .introduction {
        width: 100%;
        height: 80px;
        display: flex;
        flex-direction: row;
        font-size: 12px;
        justify-content: center;
        align-items: center;
        line-height: 20px;
        color: #999999;
        padding: 20px;
        box-sizing: border-box;
    }
}

@ant-prefix: _ant;