// 块级flex容器
.flex-container(@direction: row; @align-items: center; @justify-content: center; ) {
  display: flex;
  flex-direction: @direction;
  align-items: @align-items;
  justify-content: @justify-content;
}

// 行内flex容器
.inline-flex-container(@direction: row; @align-items: center; @justify-content: center; ) {
  .flex-container(@direction, @align-items, @justify-content);

  display: inline-flex;
}