.onceCanvasBox {
    width: auto;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    // background-color: aquamarine;
}
.canvasOutBox {
    width: 100%;
    position: relative;
}
.oncePage {
    width: 100%;
    display: flex;
    flex-shrink: 0;
    margin: 0px auto 10px auto;
    // justify-content: center;
    // align-items: center;
}
.backgroundImg {
    -webkit-user-drag: none;
    -moz-user-select: none;
    user-select: none;
    -webkit-user-select: none;
}
.canvasLayer {
    z-index: 100;
}
.addWords {
    width: 300px;
    height: 200px;
    background: #ffffff;
    cursor: auto;
    box-shadow: 0px 4px 10px 0px rgba(0, 0, 0, 0.2);
    display: flex;
    flex-direction: column;
    .addWordsBody {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        .addWordsNormalLine {
            height: 60px;
            width: 100%;
            padding: 0 20px;
            box-sizing: border-box;
            font-size: 12px;
            .addWordsInput {
                width: 230px;
                height: 30px;
                background: #ffffff;
                border: 1px solid #d9d9d9;
                border-radius: 2px;
                padding: 0 10px;
                box-sizing: border-box;
            }
            .title {
                font-size: 12px;
                color: #666666;
                letter-spacing: 0;
                margin-bottom: 5px;
                line-height: 18px;
            }
            .addWordsSwitch {
                width: 230px;
                position: relative;
                height: 30px;
                background: #ffffff;
                // border: 1px solid #d9d9d9;
                // border-radius: 2px;
                // padding: 0 10px;
                box-sizing: border-box;
                .addWordsOptionList {
                    position: absolute;
                    width: 100%;
                    flex-direction: column;
                    height: 174px;
                    background: #ffffff;
                    top: 34px;
                    z-index: 101;
                    overflow: scroll;
                    border: 1px solid gainsboro;
                }
                .addWordsOption {
                    width: 100%;
                    height: 30px;
                    display: flex;
                    padding: 0 10px;
                    font-size: 12px;
                    cursor: pointer;
                    box-sizing: border-box;
                }
                .addWordsSwichField {
                    width: 100%;
                    height: 100%;
                }
                .addWordsSwichIcon {
                    width: 25px;
                    height: 100%;
                    cursor: pointer;
                    flex-shrink: 0;
                }
                .addWordsOptionSelect {
                    background-color: #eef2fd;
                }
                .addWordsOption:hover {
                    background-color: #eef2fd;
                }
            }
        }
    }
    .addWordsFooter {
        width: 100%;
        height: 50px;
        flex-shrink: 0;
        .continueSelection {
            cursor: pointer;
            color: #0e5fd8;
            font-size: 12px;
        }
        .selectionCancel {
            width: 60px;
            height: 24px;
            cursor: pointer;
            background: #ffffff;
            border: 1px solid rgba(85, 130, 243, 1);
            border-radius: 2px;
            font-size: 12px;
            color: #5582f3;
        }
        .selectionOk {
            width: 60px;
            height: 24px;
            cursor: pointer;
            background: #5582f3;
            margin-left: 10px;
            border: 1px solid rgba(85, 130, 243, 1);
            border-radius: 2px;
            font-size: 12px;
            color: #ffffff;
        }
    }
}

@ant-prefix: _ant;