.selectArea {
    width: 100%;
    height: 100%;
    position: relative;
    background: #ffffff;
    border: 1px solid #d9d9d9;
    border-radius: 2px;
    padding: 0 10px;
    box-sizing: border-box;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.selectIcons {
    cursor: pointer;
}

.selectInput {
    width: 100%;
    height: 100%;
    display: flex;
    outline: none;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    box-sizing: border-box;
    border: 0;
}

.optionList {
    position: absolute;
    width: 100%;
    flex-direction: column;
    height: auto;
    max-height: 174px;
    background: #ffffff;
    top: 100%+20px;
    z-index: 101;
    box-sizing: border-box;
    border: 1px solid gainsboro;
    display: flex;

    .headerClickText {
        // padding: 4 10px;
        margin: 0;
        margin-bottom: 6px;

        >a {
            margin-left: 8px;
            cursor: pointer;
        }
    }

    .scrollBody {
        // flex: 1;
        overflow-y: scroll;

        &::-webkit-scrollbar {
            width: 6px !important;
        }
    }

    .tableScrollBody {
        display: flex;
        height: 100px;
        overflow: hidden;

        .left,
        .right {
            &::-webkit-scrollbar {
                width: 6px !important;
            }
        }

        .left {
            flex: 1;
            overflow-y: scroll;
        }

        .right {
            width: 50px;
            overflow-y: scroll;
        }
    }
}

.addWordsOption {
    // width: 100%;
    // height: 30px;
    display: flex;
    padding: 0 5px;
    margin-bottom: 5px;
    font-size: 12px;
    cursor: pointer;
    box-sizing: border-box;
    user-select: none;
    -moz-user-select: none;
    -webkit-user-select: none;
    justify-content: flex-start;
    align-items: center;
}

.addWordsOptionSelect {
    background-color: #eef2fd;
}

.addWordsOption:hover {
    background-color: #eef2fd;
}
@ant-prefix: _ant;