.popupMask {
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 9999;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(102, 102, 102, 0.5);
}
.errorLog {
    width: 300px;
    height: 200px;
    background-color: #FFFFFF;
    display: flex;
    justify-content: center;
    align-items: center;
}




.cqPopUp {
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 4;
    background-color: rgba(0, 0, 0, 0.4);
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}
.cqPopUpBox {
    width: 460px;
    height: 400px;
    min-height: 340px;
    box-shadow: 0 2px 8px 0 rgb(0 0 0%);
    background-color: #fff;
    border-radius: 6px;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: space-between;
}
.cqPopUpNav {
    width: 100%;
    height: 50px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 0 20px;
}
.cqPopUpNav:before {
    content: "";
    width: 6px;
    height: 6px;
    border-radius: 50%;
    background-color: #276ff5;
}
.cqPopUpNavText {
    width: 100%;
    height: 100%;
    padding-left: 8px;
    font-size: 18px;
    display: flex;
    align-items: center;
}
.cqPopUpBody {
    width: 100%;
    height: 200px;
    display: flex;
    font-size: 14px;
    flex-direction: column;
}
.cqPopUpBodyImg {
    width: 100%;
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.cqPopUpBodyMessage {
    width: 100%;
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.cqPopUpFooter {
    width: 100%;
    height: 90px;
    display: flex;
    justify-content: center;
    align-items: center;
    >button {
        outline: none;
    }
}
.cqPopUpFooterBtn1 {
    width: 75px;
    padding: 0 8px;
    line-height: 28px;
    height: 28px;
    border-radius: 2px;
    color: #666;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    cursor: pointer;
    border: 1px solid #bbb;
}
.cqPopUpFooterBtn2 {
    width: 75px;
    padding: 0 8px;
    line-height: 28px;
    height: 28px;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    cursor: pointer;
    min-width: 60px;
    margin-left: 12px;
    border-radius: 2px;
    color: #ffffff;
    border: 1px solid #5582F3;
    background-color: #5582F3;;
}

@ant-prefix: _ant;