.inTheMiddle {
    display: flex;
    justify-content: center;
    align-items: center;
}
.around_center {
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.between_center {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.start_center {
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.column_center_start {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}

@ant-prefix: _ant;