.errorList {
    width: 100%;
    height: 100%;
    font-size: 12px;
    line-height: 18px;
    overflow: hidden;
    // position: absolute;
    color: #666666;
    background-color: #fff;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    >img {
        user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        -webkit-user-select: none;
        -webkit-user-drag: none;
    }
}

.mainError {
    font-size: 14px;
    margin-top: 16px;
    color: #999999;
    overflow: hidden;
    word-break: break-all;
}

.errorInfoBox {
    margin-top: 8px;
    width: 220px;
    height: auto;
    /* text-indent: 24px; */
    display: flex;
    flex-direction: column;
}

.comparison {
    width: 70px;
    height: 30px;
    display: flex;
    background-color: #5582f3;
    color: #ffffff;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    border-radius: 3px;
    margin-top: 50px;
}

.errorInfo {}
@ant-prefix: _ant;