@import url('../../themes/mixins.less');

.table-wrapper {
  .title {
    .flex-container(row, center, space-between);

    .icon {
      width: 16px;
      height: 16px;
      transform: rotate(180deg);
      cursor: pointer;
    }
  }

  .table-area {
    width: 100%;
    overflow-x: auto;

    .table {
      .line {
        .cell {
          text-align: center;
          font-size: 12px;
        }
      }

      .line-head {
        height: 40px;
        background-color: #ccc;
      }

      .line-body {
        height: 50px;

        .cell-input {
          width: 140px;
          font-size: 12px;
          text-align: center;
        }
      }
    }

    .t-table {
      .t-line {
        height: 50px;

        .t-cell-head {
          background-color: #ccc;
        }

        .t-cell {
          text-align: center;
          font-size: 12px;

          .text {
            width: 140px;
          }

          .t-cell-input {
            width: 140px;
            font-size: 12px;
            text-align: center;
          }
        }

      }
    }
  }
}
@ant-prefix: _ant;