.mark {
    width: 100%;
    height: 100%;
    position: absolute;
    background: rgba(102, 102, 102, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
}
.AttachmentBox {
    width: 960px;
    height: 563px;
    background: #ffffff;
    display: flex;
    flex-direction: column;
}
.AttachmentHead {
    width: 100%;
    height: 49px;
    flex-shrink: 0;
    padding: 0 20px;
    box-sizing: border-box;
    border-bottom: 1px solid #d9d9d9;
    display: flex;
    justify-content: space-between;
    /* justify-leftContent: center; */
    align-items: center;
    font-size: 18px;
    font-weight: bold;
}
.closeImg {
    width: 14px;
    cursor: pointer;
}
.AttachmentBody {
    width: 100%;
    height: 100%;
    display: flex;
    overflow: hidden;
    flex-direction: column;
}
.AttachmentPageLabel {
    width: 100%;
    flex-shrink: 0;
    height: 30px;
    display: flex;
    padding: 0 20px;
    align-items: flex-end;
    font-size: 12px;
    box-sizing: border-box;
    user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    -webkit-user-select: none;
    cursor: pointer;
    .labels {
        width: 90px;
        height: 24px;
        border: 1px solid;
        border-radius: 2px;
        margin-right: 1px;
        display: flex;
        justify-content: center;
        align-items: center;
        overflow: hidden;
        -webkit-transition: background-color 0.2s cubic-bezier(0.42, 0, 1, 1);
        transition: background-color 0.2s cubic-bezier(0.42, 0, 1, 1);
    }
}
.AttachmentFooter {
    width: 100%;
    height: 50px;
    position: relative;
    flex-shrink: 0;
    border-top: 1px solid #d9d9d9;
    display: flex;
    justify-content: center;
    align-items: center;
    > span {
        font-size: 12px;
        color: #6666;
        position: absolute;
        left: 20px;
    }
}
.AttachmentCancelBtn {
    width: 70px;
    height: 26px;
    border-radius: 2px;
    color: #333333;
    border: 1px solid #d9d9d9;
    background-color: #ffffff;
    margin-right: 12px;
    cursor: pointer;
}
.AttachmentConfirmBtn {
    width: 70px;
    height: 26px;
    border-radius: 2px;
    color: #ffffff;
    border: 1px solid #5582f3;
    background-color: #5582f3;
    margin-right: 12px;
    cursor: pointer;
}

@ant-prefix: _ant;