@import url('../../themes/mixins.less');

.listArea {
    width: 460px;
    height: 100%;
    display: flex;
    flex-direction: column;
    border-left: 1px solid #e5e5e5;
    flex-shrink: 0;

    .listNav {
        height: 50px;
        width: 100%;
        flex-shrink: 0;
        background: #ffffff;
        box-sizing: border-box;
        padding: 0 20px;
        border-bottom: 1px solid #e5e5e5;

        >span {
            font-size: 14px;
        }
    }

    .listBody {
        height: calc(100% - 100px);
        width: 100%;
        padding: 10px;
        background: #ffffff;
        display: flex;
        position: relative;
        flex-direction: column;
        height: 100%;
        overflow-y: scroll;

        :global {
            .ant-tabs {
                height: 100%;

                .ant-tabs-content {
                    height: 100%;

                    .ant-tabs-tabpane {
                        height: 100%;

                        .kd-empty {
                            height: 100%;
                            justify-content: center;
                            align-items: center;
                        }
                    }
                }

            }
        }

        .nav {
            height: 50px;
            width: 100%;
            flex-shrink: 0;
            font-size: 12px;

            .confidence {
                >span {
                    margin-left: 4px;
                }
            }
        }

        .content {
            height: 100%;
            width: 100%;
            position: relative;
            overflow: scroll;
            margin-top: 10px;
        }

        .listItem {
            height: 50px;
            font-size: 12px;
            padding: 0 20px;
            background-color: #ffffff;

            .listItemTitle {
                width: 120px;
                height: 100%;
                flex-shrink: 0;

                >img {
                    margin-right: 4px;
                }

                .whiteSpaceHold {
                    width: 17px;
                }
            }

            .verifyFailed {
                border: 1px solid red !important;
            }

            .listItemSelection {
                width: 100%;
                position: relative;
                background-color: #ffffff;
                height: 30px;
                border: 1px solid rgba(217, 217, 217, 1);
                border-radius: 2px;
                padding: 0 10px;

                >input {
                    width: 100%;
                    height: 28px;
                    border: 0;
                    outline: none;
                }

                .selectionBtn {
                    width: 20px;
                    height: 100%;
                    border: 0;
                    cursor: pointer;
                    display: flex;
                    justify-content: center;
                    align-items: center;

                    &::before {
                        font-size: 14px;
                    }
                }

                .optionList {
                    position: absolute;
                    width: 100%;
                    flex-direction: column;
                    height: auto;
                    background: #ffffff;
                    top: 34px;
                    z-index: 101;
                    border: 1px solid gainsboro;
                }

                .option {
                    width: 100%;
                    height: 30px;
                    display: flex;
                    padding: 0 10px;
                    font-size: 12px;
                    cursor: pointer;
                    box-sizing: border-box;
                }

                .optionSelect {
                    background-color: #eef2fd;
                }

                .option:hover {
                    background-color: #eef2fd;
                }
            }

            // &:hover {
            //     background: #f2f9ff;
            //     border: 1px solid rgba(224, 239, 255, 1);
            // }
        }

        .twinkle {
            animation: twinkle 1s linear;
        }

        @keyframes twinkle {
            0% {
                background-color: #ffffff;
                border: 1px solid rgba(217, 217, 217, 1);
            }

            25% {
                background: #f2f9ff;
                border: 1px solid rgba(224, 239, 255, 1);
            }

            50% {
                background-color: #ffffff;
                border: 1px solid rgba(217, 217, 217, 1);
            }

            75% {
                background: #f2f9ff;
                border: 1px solid rgba(224, 239, 255, 1);
            }

            100% {
                background-color: #ffffff;
                border: 1px solid rgba(217, 217, 217, 1);
            }
        }

        @-webkit-keyframes twinkle {
            0% {
                background-color: #ffffff;
                border: 1px solid rgba(217, 217, 217, 1);
            }

            25% {
                background: #f2f9ff;
                border: 1px solid rgba(224, 239, 255, 1);
            }

            50% {
                background-color: #ffffff;
                border: 1px solid rgba(217, 217, 217, 1);
            }

            75% {
                background: #f2f9ff;
                border: 1px solid rgba(224, 239, 255, 1);
            }

            100% {
                background-color: #ffffff;
                border: 1px solid rgba(217, 217, 217, 1);
            }
        }

        .listSelected {
            background: #f2f9ff !important;
            border: 1px solid rgba(224, 239, 255, 1) !important;
            box-sizing: border-box;
            padding: 0 19px;
        }
    }

    .listFooter {
        height: 50px;
        width: 100%;
        flex-shrink: 0;
        background: #ffffff;

        // border-top: 1px solid #e5e5e5;
        .backRun,
        .cancel {
            width: 64px;
            height: 28px;
            cursor: pointer;
            border: 1px solid rgba(85, 130, 243, 1);
            border-radius: 2px;
            font-size: 12px;
        }

        .cancel {
            background: #ffffff;
            color: #5582f3;
            margin-right: 20px;
        }

        .backRun {
            background: #5582f3;
            color: #ffffff;
        }

        .save {
            width: 64px;
            height: 28px;
            cursor: pointer;
            background: #ffffff;
            border: 1px solid rgba(85, 130, 243, 1);
            border-radius: 2px;
            font-size: 12px;
            color: #5582f3;
        }

        .importDoc {
            width: 64px;
            height: 28px;
            cursor: pointer;
            background: #5582f3;
            margin-left: 10px;
            border: 1px solid rgba(85, 130, 243, 1);
            border-radius: 2px;
            font-size: 12px;
            color: #ffffff;
        }
    }
}

.iconBtn {
    cursor: pointer;

    &:hover {
        color: #5582f3;
    }
}

.hiddenList {
    display: none !important;
}

.tabs {
    // height: 100%;
    // overflow: scroll;

    :global {
        .ant-tabs-nav {
            margin: 0 !important;
        }
    }
}

.infoListNav {
    display: flex;
    // height: 30px;

    align-items: center;

    .navTitle {
        margin-left: 20px;
        font-size: 14px;
        cursor: pointer;
    }
}

.label {
    padding: 0 20px;
    font-weight: bold;
    .flex-container(row, center, space-between);

    .labelBefore {
        height: 100%;
        margin-right: 5px;
        font-weight: inherit;
        cursor: pointer;
        .flex-container();
    }

    .labelAfter {
        position: relative;
        height: 100%;
        margin-left: 5px;
        .flex-container();

        .labelAfterTips {
            width: 280px;
            height: auto;
            flex-direction: column;
            background-color: white;
            position: absolute;
            top: 30px;
            left: -216px;
            filter: drop-shadow(0 0 2px #999);
            border: dashed transparent;
            z-index: 1;
            font-size: 12px;
            line-height: 20px;
            font-weight: 500;
            border-radius: 2px;
            padding: 0 5px;

            &::before {
                content: '';
                border: 7px solid transparent;
                border-bottom-color: white;
                position: absolute;
                right: 5px;
                bottom: 62px;
            }

            .labelAfterTipsImgs {
                .flex-container(row, center, space-between);
            }
        }
    }

    .llmSvg {
        margin-left: 5px;
    }

    .crossSvg {
        width: 16px;
        height: 16px;
        transform: rotate(90deg);
    }
}

.allList,
.normalList,
.tableList {

    // width: 100%;
    height: 100%;
    overflow-y: scroll;

    :global {
        .ant-collapse-content-box {
            padding: 0 !important;
        }
    }

    .header {
        .flex-container(row, center, space-between);
    }

    .llmHeader {
        display: flex;

        .llmSvg {
            margin-left: 5px;
        }
    }
}

.tableList {
    width: 100%;
    height: 100%;
}

.tableArea {
    padding: 20px 20px;
}

.tableInfo {
    width: 100%;
    height: auto;
    font-size: 12px;
    table-layout: fixed;

    >thead {
        .tableLine {
            .tableUnit {
                padding: 10px 0;
                text-align: center;
            }
        }
    }

    >tbody {
        .tableLine {

            // .tableTitle {
            //     min-height: 20px;
            //     position: relative;
            //     text-align: center;
            //     & ::before {
            //         position: absolute;
            //         content: "";
            //         display: block;
            //         background-color: black;
            //         height: 50px;
            //         width: 50px;
            //     }
            // }
            .tableUnit {
                min-height: 20px;
                text-align: center;

                >input {
                    outline: none;
                    width: 100%;
                    height: 100%;
                    padding: 4px 8px;
                    box-sizing: border-box;
                    border: 0;
                }
            }
        }
    }
}

:global {
    .header-right-tips {
        max-width: 300px !important;
        font-size: 12px !important;

        .header-right-tips-content {
            .flex-container(row, center, space-between);

        }
    }

    .llm-header-left-tips {
        font-size: 12px !important;

    }
}
@ant-prefix: _ant;