@import url("../color.less");
@import url('../../themes/mixins.less');
@uploadFilePathWidth: 160px;
@uploadfilePathHeight: 220px;

.uploadFileBox {
    height: 500px;
    width: 530px;
    flex-direction: column;
}

.uploadArea {
    height: 350px;
    width: 100%;
    flex-direction: column;

    .uploadMark {
        width: @uploadFilePathWidth;
        height: @uploadfilePathHeight;
        overflow: hidden;
        opacity: 0.5;
        position: absolute;
    }

    .uploadDesc {
        font-size: 12px;
        line-height: 20px;
        color: #999999;
        margin-top: 5px;
    }

    .uploadSlider {
        width: @uploadFilePathWidth;
        height: @uploadfilePathHeight*0.2;
        margin-top: -50px;
        background: linear-gradient(@backgroundColor_white, @upload_loadingSlide 100%);
        animation: sliderMove 3s linear;
        -webkit-animation: sliderMove 3s infinite;
    }

    .uploadFilePath {
        width: @uploadFilePathWidth;
        height: @uploadfilePathHeight;
        display: flex;
        flex-shrink: 0;
        justify-content: center;
        align-items: center;
        box-sizing: border-box;
        background: @backgroundColor_uploadFilePath;
        color: @upload_FileName;
        font-size: 14px;
    }
}

.fileInput {
    visibility: hidden;
    height: 0;
    width: 0;
}

@keyframes sliderMove {
    from {
        margin-top: -50px;
    }

    to {
        margin-top: 270px;
    }
}

/*Safari and Chrome*/
@-webkit-keyframes sliderMove {
    from {
        margin-top: -50px;
    }

    to {
        margin-top: 270px;
    }
}

.uploadFileComponent {
    width: 100%;
    height: 100%;
}

.noDrag {
    border: 1px dashed @upload_border_noDrag;
}

.onDragIn {
    border: 2px dashed @upload_border_onDrag;
}

.forbidenAttachment {
    width: 100%;
    height: 100%;
    flex-direction: column;
    cursor: pointer;
}

.icon {
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 4px;
}

.supportAttachment {
    width: 100%;
    height: 100%;
    position: relative;
    flex-direction: column;
}

.uploadIconArea {
    width: 100%;
    height: 100%;
    position: absolute;
    flex-direction: column;
    background-color: @backgroundColor_uploadFilePath;
}

.uploadIcon {
    width: 146px;
    height: 98px;
    font-size: 14px;
    cursor: pointer;
    flex-direction: column;
    color: @upload_icon_normalColor;
    background: @upload_icon_normalBackGround;
}

.uploadIcon:hover {
    color: @upload_icon_hoverColor;
    background: @upload_icon_hoverBackGround;
}

.attachmentNav {
    height: 50px;
    width: 100%;
    flex-shrink: 0;
}

.attachmentBody {
    width: 100%;
    height: 100%;
}

.fileInfo {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
}

.fileInfoBody {
    width: 100%;
    height: 100%;
    flex-direction: column;

    >img {
        width: 45px;
        height: 55px;
    }

    >span {
        font-size: 12px;
        max-width: 110px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        line-height: 25px;
        margin-top: 10px;
        color: @fileInfo_font_color;
    }
}

.fileInfoFooter {
    width: 100%;
    height: 50px;
    flex-shrink: 0;
    position: relative;

    .uploadTips {
        width: auto;
        height: auto;
        flex-direction: column;
        background-color: white;
        position: absolute;
        bottom: 40px;
        left: 60px;
        filter: drop-shadow(0 0 2px #999);
        border: dashed transparent;
        z-index: 1;
        font-size: 12px;
        line-height: 20px;
        font-weight: 500;
        border-radius: 2px;

        &::before {
            content: "";
            border: 7px solid transparent;
            border-top-color: white;
            position: absolute;
            left: 5px;
            bottom: -16px;
        }
    }

    .uploadTipsBtn {
        width: 86px;
        height: 44px;
        color: #212121;
        background-color: #ffffff;
        outline: none;
        border: 0;
        cursor: pointer;
    }

    .uploadTipsBtn:hover {
        background-color: #f2f9ff;
        color: #5582f3;
    }

    .rotateIcon {
        font-size: 15px;
        animation: rotation 1s linear;
        -webkit-animation: rotation 1s infinite;
    }

    @keyframes rotation {
        0% {
            transform: rotate(0deg);
        }

        100% {
            transform: rotate(360deg);
        }
    }

    /*Safari and Chrome*/
    @-webkit-keyframes rotation {
        0% {
            transform: rotate(0deg);
        }

        100% {
            transform: rotate(360deg);
        }
    }
}

.uploadAndPageSelected {
    display: flex;
    width: 100%;
    height: 100%;
}

//pageSelected

.pageSelected {
    height: 220px;
    width: 100%;
    flex-direction: column;
    justify-content: space-around;
    background-color: @backgroundColor_pageSelected;
    border: 1px solid @pageSelected_border;

    .pageSelectedBody {
        width: 100%;
        // height: 100%;
        padding: 0 20px;
        box-sizing: border-box;
        display: flex;
        flex-direction: column;

        .select {
            margin-bottom: 10px;
        }

        .pageSelectedBodyNav {
            width: 100%;
            height: 30px;
            flex-shrink: 0;
            font-weight: bold;
        }

        .pageSelectedBodyContent {
            width: 100%;
            // height: 100%;
            flex-direction: column;

            .fixedSelection {
                height: 30px;
                width: 100%;
            }

            .freeSelection {
                height: 40px;
                width: 100%;
            }
        }
    }

    .pageSelectedFooter {
        width: 100%;
        // height: 50px;
        flex-shrink: 0;

        .extractBtn {
            width: 330px;
            height: 28px;
            background: #5583f3;
            border-radius: 2px;
            font-size: 12px;
            color: #ffffff;
            cursor: pointer;
        }

        .extractBtn:hover {
            box-shadow: 0 0 5px 1px #87adff;
        }

        .extractBtnGray {
            width: 330px;
            height: 28px;
            background: #bacbf7;
            border-radius: 2px;
            font-size: 12px;
            color: #ffffff;
            cursor: no-drop;
        }
    }

    .freeSelectionInput {
        // width: 240px;
        flex-grow: 1;
        height: 28px;
        background: #ffffff;
        border: 1px solid #d9d9d9;
        border-radius: 2px;
        padding: 0 10px;
        margin-left: 10px;
    }

    .freeSelectionTips {
        width: 200px;
        height: auto;
        flex-direction: column;
        background-color: white;
        position: absolute;
        top: -55px;
        left: -5px;
        filter: drop-shadow(0 0 2px #999);
        border: dashed transparent;
        z-index: 1;
        font-size: 12px;
        line-height: 20px;
        font-weight: 500;
        border-radius: 2px;
        padding: 0 5px;

        &::before {
            content: "";
            border: 7px solid transparent;
            border-top-color: white;
            position: absolute;
            left: 5px;
            bottom: -16px;
        }
    }
}

.radioBtn {
    display: flex;
    width: auto;
    height: 100%;
    align-items: center;
    flex-grow: 1;

    .radio {
        width: 12px;
        height: 12px;
        border: 1px solid #d9d9d9;
        border-radius: 50%;
        margin-right: 5px;
        cursor: pointer;
    }

    .radioSelected {
        width: 12px;
        height: 12px;
        border: 1px solid #5582f3;
        border-radius: 50%;
        margin-right: 5px;
        cursor: pointer;
    }

    .radioInner {
        width: 0px;
        height: 0px;
        border-radius: 50%;
        background: #5582f3;
        cursor: pointer;
    }

    .radioInnerSelected {
        width: 8px;
        height: 8px;
        border-radius: 50%;
        background: #5582f3;
        cursor: pointer;
    }

    >span {
        font-size: 12px;
        color: #212121;
        line-height: 18px;
    }
}

